:root {
  /* キーカラー */
  --primary: #85BCC7;
  --primary-dark: #6fa3ae;
  --bg-base: #f8f9fa; /* より白に近いグレーでクリーンに */
  --text-main: #1F2937; /* 真っ黒ではなくダークグレーで目に優しく */
  --text-muted: #6B7280;
}

/* 全体に角ゴシックを適用 */
body {
  font-family: 'Zen Kaku Gothic New', sans-serif;
  background-color: var(--bg-base);
  color: var(--text-main);
  /* 紙のような質感ノイズ（薄く残す） */
