  background-attachment: fixed;
  -webkit-font-smoothing: antialiased;
}

/* 見出しも同じフォントで太さを変えて表現 */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Zen Kaku Gothic New', sans-serif;
  font-weight: 700; /* Boldで力強く */
  letter-spacing: 0.05em; /* 少し広げて読みやすく */
}

